@import '../../../sass/abstracts/variables';
@import '../../../sass/abstracts/mixins';
.footer{
    background-color: $color-dark-grey-3;
    padding:10rem 0;
    font-size: 1.4rem;
    @include respond(tab-land){
        padding:8rem 0;
        font-size: 1.4rem;
    }
    @include respond(tab-port){
        padding:8rem 0;
        font-size: 1.3rem;
    }
    @include respond(phone){
        padding:5rem 0;
        font-size: 3rem;
    }
    &__logo-box{
        text-align: center;
        margin-bottom: 8rem;
        
    }
    &__logo{
        @include respond(tab-land){
            width: 10%;
            text-align: center;
        }
    }
    &__navigation{
        border-top: 1px solid $color-light-grey-2;
        padding-top: 2rem;
        display: inline-block;
        @include respond(tab-port){
            width: 100%;
            text-align: center;
        }
        
    }
    &__list{
        list-style:none;
    }
    &__item{
        display: inline-block;
        &:not(:last-child){
            margin-right: 1.5rem;
        }
    }
    &__link{
        
        &:link,
        &:visited{
            color:$color-light-grey;
            text-decoration: none;
            text-transform: uppercase;
            display: inline-block;
            transition: all .2s ;
            
        }
        &:hover,
        &:active{
            color: $color-primary;
            box-shadow: 0 1rem 2rem rgba($color-black,.4);
            transform: rotate(5deg) scale(1.3); 
            background-color: $color-dark-grey-3;
        }
    }
    &__copyright{
        border-top: 1px solid $color-light-grey-2;
        padding-top: 2rem;
        width: 80%;
        float: right;
        @include respond(tab-port){
            width: 100%;
            float: none;
        }
    }
}