@import '../../../sass/abstracts/variables';
.card{
    //FUNCTIONALITY
    perspective:150rem;
   -moz-perspective: 150rem;
    position: relative;
    height:52rem;
    &__side{
        
        height: 50rem;
        transition: all 0.8s ease;
        position: absolute;
        top:0;
        left:0;
        width: 100%;
        backface-visibility: hidden;
        border-radius: 5px;
        box-shadow: 0 1.5rem 4rem rgba($color-black,.6);
        overflow: hidden;
        &--front{
            background-color: $color-white
        }
        &--back{
            
            transform: rotateY(-180deg);
            &--1{
                background-image: linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark);
            }
            &--2{
                background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
            }
            &--3{
                background-image: linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark);
            }

        }
    }

    &:hover &__side{
        &--front{
            transform: rotateY(180deg);
        }
        &--back{
            transform: rotateY(0deg);
        }
        
    }

    //FRONT DESIGN
    &__picture{
        background-size: cover;
        height: 23rem;
        background-blend-mode: screen;
        -webkit-clip-path: polygon(0 0,100% 0, 100% 85%, 0 100%);
        clip-path: polygon(0 0,100% 0, 100% 85%, 0 100%);

        &--1{
            background-image:linear-gradient(to right bottom, $color-secondary-light, $color-secondary-dark), url(../../../assets/ghumoAssets/img/nat-5.jpg);
        }
        &--2{
            background-image:linear-gradient(to right bottom, $color-primary-light, $color-primary-dark), url(../../../assets/ghumoAssets/img/nat-6.jpg);

        }
        &--3{
            background-image:linear-gradient(to right bottom, $color-tertiary-light, $color-tertiary-dark), url(../../../assets/ghumoAssets/img/nat-7.jpg);

        }
    }

    &__heading{
        text-transform: uppercase;
        font-weight: 300;
        font-size: 2.9rem;
        color: $color-white;
        position: absolute;
        text-align: right;
        top:12rem;
        right: 1.8rem;
    }

    &__heading--span{
        padding: 1rem 2.3rem;
        -webkit-box-decoration-break:clone;
        box-decoration-break: clone;
        
        &--1{
            background-image: linear-gradient(to right bottom, rgba($color-secondary-light,0.85), rgba($color-secondary-dark,0.85));
        }
        &--2{
            background-image: linear-gradient(to right bottom, rgba($color-primary-light,0.85), rgba($color-primary-dark,0.85));
        }
        &--3{
            background-image: linear-gradient(to right bottom, rgba($color-tertiary-light,0.85), rgba($color-tertiary-dark,0.85));
        }
    }

    &__details{
        padding: 3rem;
        ul{
            list-style: none;
            width: 80%;
            margin:0 auto;
            li{
                text-align: center;
                font-size: 1.5rem;
                padding: 1rem;
                &:not(:last-child){
                    border-bottom: 1px solid $color-light-grey-2;
                }
            }
        }
    }
    &__cta{
        position: absolute;
        top:50%;
        left:50%;
        transform: translate(-50%,-50%);
        width:100%;
        text-align: center;
    }
    &__price-box{
        text-align: center;
        color: $color-white;
        margin-bottom: 8rem;
    }
    &__price-only{
        font-size: 1.4rem;
        text-transform: uppercase;
    }
    &__price-value{
        font-size: 6rem;
        font-weight: 100;
    }
    &__btn{
        font-size: 1.0rem !important;
        padding: 1rem 1rem !important;
    }
}