@import '../../../sass/abstracts/variables';
@import '../../../sass/layout/grid';
@import '../../../sass/layout/grid';
@import '../../../sass/abstracts/mixins';
.section-about{
    background-color: $color-light-grey;
    padding: 20rem 0;
   // padding-top:20rem;
    margin-top: -20vh;
   // padding-bottom: -20rem;
   @include respond(tab-port){
     margin-bottom: 30vh;
    }
    height: 110rem;
    .composition{
        position: relative;
        &__photo{
            width:55%;
            box-shadow: 0 1.5rem 4rem rgba($color-black,0.4);
            border-radius: 2px;
            position: absolute;
            z-index:10;
            transition: all 0.2s;
            outline-offset: 2rem;

            @include respond(tab-port){
                float: left;
                position: relative;
                width: 33.333333%;
                box-shadow: 0 1.5rem 3rem ;
            }
            &--p1{
                left:0;
                top:-2rem;
                @include respond(tab-port){
                    top: 0;
                    transform: scale(1.1);
                }
            }
            &--p2{
                right:0;
                top:2rem;
                @include respond(tab-port){
                    top: -1rem;
                    transform: scale(1.3);
                    z-index: 300;
                }
            }
            &--p3{
                left:20%;
                top:10rem;
                @include respond(tab-port){
                    top: 1rem;
                    left:0;
                    transform: scale(1.1);
                }
            }

            &:hover{
                outline: 1.5rem solid $color-primary;
                z-index: 20;
                transform: scale(1.05);
                box-shadow: 0 2.5rem 4rem rgba($color-black,0.5);
            }
        }
        &:hover &__photo:not(:hover){
            transform:scale(0.95);
        }
    }
}   